button{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    padding : .7rem 1rem;
    border: none;
    outline: none;
    background-color: transparent;
    color: inherit;
    font-family: inherit;
    border-radius: 6px;
    transition: all .4s ease;
    cursor: pointer;

    
}
button span{
    display: block;
}
button img{
    width: 1rem;
    margin-top: .2rem;
}
.default{
    color: var(--btn-default-color);
    background: var(--btn-default-bg);
    box-shadow: var(--btn-default-shadow);
    background-color: var(--btn-default-bg);
   
}
.default:hover,
.default:focus{
    background-color: var(--btn-default-bg-hover);
}
.primary{
    color: #FFFFFF;
    background: #2962FF;
    box-shadow: 0px 2px 3px rgba(41, 98, 255, 0.2);
}
.primary:hover{
    background: #0039CB;
}

.secondary{
    background: #455A64;
    box-shadow: 0px 2px 3px rgba(69, 90, 100, 0.2);
    color: #FFFFFF;
}
.secondary:hover{
    background: #1C313A;
}

.danger{
    color: #FFFFFF;
    background: #D32F2F;
    box-shadow: 0px 2px 3px rgba(211, 47, 47, 0.2);
}
.danger:hover{
    background: #9A0007;
}



.outline{
    background-color : var(--btn-outline-bg);
    box-shadow: var(--btn-outline-shadow);
    border : 1px solid var(--btn-outline-color);
    color :  var(--btn-outline-color);
}
.outline:hover,
.outline:focus{
    background: var(--btn-outline-bg-hover);
}

.text{
    background-color: transparent;
    box-shadow: none;
    border: none;
    color: #3D5AFE;
}
.text:hover,
.text:focus{
    background: var(--btn-outline-bg-hover);
}
.disabled{
    color: var(--btn-disabled-color);
    background-color: var(--btn-default-bg);
    box-shadow: none;
    cursor: unset;
    
}
.disabled.text{
    background-color: transparent;
}
