@import url(https://fonts.googleapis.com/css2?family=Fira+Mono&display=swap);

*, *::after, *::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
:root{
  --btn-default-bg :#E0E0E0;
  --btn-default-bg-hover : #AEAEAE;
  --btn-default-color :#3F3F3F;
  --btn-default-shadow : 0px 2px 3px rgba(51, 51, 51, 0.2);

  --btn-disabled-color : #9E9E9E;

  --btn-outline-bg : transparent;
  --btn-outline-color : #3D5AFE;
  --btn-outline-bg-hover :rgba(41, 98, 255, 0.1);
  --btn-outline-shadow : none;

}

body{
  font-family: 'Fira Mono', monospace;
  min-height: 100vh;
  overflow: hidden;
  padding : 2rem 1rem;
  display: flex;
  justify-content: center;
  align-items: center;

}

#root{
  width: 100%;

}
button{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: .5rem;
    gap: .5rem;
    padding : .7rem 1rem;
    border: none;
    outline: none;
    background-color: transparent;
    color: inherit;
    font-family: inherit;
    border-radius: 6px;
    transition: all .4s ease;
    cursor: pointer;

    
}
button span{
    display: block;
}
button img{
    width: 1rem;
    margin-top: .2rem;
}
.default{
    color: var(--btn-default-color);
    background: var(--btn-default-bg);
    box-shadow: var(--btn-default-shadow);
    background-color: var(--btn-default-bg);
   
}
.default:hover,
.default:focus{
    background-color: var(--btn-default-bg-hover);
}
.primary{
    color: #FFFFFF;
    background: #2962FF;
    box-shadow: 0px 2px 3px rgba(41, 98, 255, 0.2);
}
.primary:hover{
    background: #0039CB;
}

.secondary{
    background: #455A64;
    box-shadow: 0px 2px 3px rgba(69, 90, 100, 0.2);
    color: #FFFFFF;
}
.secondary:hover{
    background: #1C313A;
}

.danger{
    color: #FFFFFF;
    background: #D32F2F;
    box-shadow: 0px 2px 3px rgba(211, 47, 47, 0.2);
}
.danger:hover{
    background: #9A0007;
}



.outline{
    background-color : var(--btn-outline-bg);
    box-shadow: var(--btn-outline-shadow);
    border : 1px solid var(--btn-outline-color);
    color :  var(--btn-outline-color);
}
.outline:hover,
.outline:focus{
    background: var(--btn-outline-bg-hover);
}

.text{
    background-color: transparent;
    box-shadow: none;
    border: none;
    color: #3D5AFE;
}
.text:hover,
.text:focus{
    background: var(--btn-outline-bg-hover);
}
.disabled{
    color: var(--btn-disabled-color);
    background-color: var(--btn-default-bg);
    box-shadow: none;
    cursor: unset;
    
}
.disabled.text{
    background-color: transparent;
}

#app{

    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(5rem, 7rem));

    grid-gap: 2rem;

    gap: 2rem;
    justify-content: center;
    align-items: center;
    padding-bottom: 10rem;

}
